import { createSignal, For, Show, type Component } from 'solid-js';
import { MappingItem } from './MappingItem';
import type { SttStore } from '../stores/sttStore';
import { filterRecordsByDuration, groupRecordsByDay, getRecordsForType } from '../converters/stt-uhabits/sttParser';

interface Props {
  disabled: boolean;
  sttStore: SttStore;
  onConvert: () => void;
}

interface MappingRef {
  id: number;
  ref: any;
}

export const SttControls: Component<Props> = (props) => {
  const [mappings, setMappings] = createSignal<MappingRef[]>([]);
  const [nextId, setNextId] = createSignal(0);

  // Add initial mapping
  setTimeout(() => addMapping(), 0);

  const addMapping = () => {
    const id = nextId();
    setNextId(id + 1);
    setMappings([...mappings(), { id, ref: null }]);
  };

  const removeMapping = (id: number) => {
    setMappings(mappings().filter(m => m.id !== id));
    updateSummary();
  };

  const updateSummary = () => {
    // Trigger summary recalculation
  };

  const calculateNewDaysForMapping = (sttTypeId: number, uhabitsHabitId: number, minDuration: number): number => {
    const sttData = props.sttStore.sttData();
    const uhabitsData = props.sttStore.uhabitsData();
    
    if (!sttData || !uhabitsData) return 0;
    
    const filteredRecords = filterRecordsByDuration(sttData.records, minDuration);
    const typeRecords = getRecordsForType(filteredRecords, sttTypeId);
    const dayGroups = groupRecordsByDay(typeRecords);

    const existingDates = new Set<string>();
    for (const rep of uhabitsData.repetitions) {
      if (rep.habit_id === uhabitsHabitId && rep.value > 0) {
        existingDates.add(new Date(rep.timestamp).toISOString().split('T')[0]);
      }
    }

    let newDays = 0;
    for (const dayStr of dayGroups.keys()) {
      if (!existingDates.has(dayStr)) newDays++;
    }
    return newDays;
  };

  const summaryData = () => {
    const sttData = props.sttStore.sttData();
    const uhabitsData = props.sttStore.uhabitsData();

    if (!sttData || !uhabitsData) return null;

    let validMappings = 0;
    let totalNewDays = 0;
    const mappingDetails: string[] = [];

    for (const mapping of mappings()) {
      const mappingData = mapping.ref?.getMapping?.();
      if (!mappingData) continue;

      const { sttTypeId, uhabitsHabitId, minDuration } = mappingData;
      
      if (sttTypeId && uhabitsHabitId) {
        validMappings++;

        const sttType = sttData.recordTypes.get(sttTypeId);
        const uhabit = uhabitsData.booleanHabits.get(uhabitsHabitId);

        if (sttType && uhabit) {
          const newDays = calculateNewDaysForMapping(sttTypeId, uhabitsHabitId, minDuration);
          totalNewDays += newDays;
          mappingDetails.push(`"${sttType.emoji} ${sttType.name}"  "${uhabit.name}": ${newDays} new days`);
        }
      }
    }

    return validMappings > 0 ? { totalNewDays, mappingDetails } : null;
  };

  const canConvert = () => {
    return props.sttStore.sttData() && props.sttStore.uhabitsData() &&
      props.sttStore.sttFile() && props.sttStore.uhabitsFile() &&
      mappings().length > 0;
  };

  return (
    <section id="action-stt-uhabits-fill" class="controls-block">
      <h3>SimpleTimeTracker  uHabits: Fill</h3>

      <div id="conversion-mappings">
        <h4>Activity Mappings</h4>
        <div id="mapping-list">
          <For each={mappings()}>
            {(mapping) => (
              <MappingItem
                ref={(el: any) => (mapping.ref = el)}
                mappingId={mapping.id}
                sttData={props.sttStore.sttData()}
                uhabitsData={props.sttStore.uhabitsData()}
                onRemove={() => removeMapping(mapping.id)}
                onUpdate={updateSummary}
              />
            )}
          </For>
        </div>
        <button id="add-mapping" type="button" onClick={addMapping}>
          + Add Mapping
        </button>
      </div>

      <Show when={summaryData()}>
        {(summary) => (
          <div id="conversion-summary" class="summary-box">
            <h4>Conversion Summary</h4>
            <ul>
              <li>{summary().totalNewDays} new repetitions will be added</li>
              <For each={summary().mappingDetails}>
                {(detail) => <li>{detail}</li>}
              </For>
            </ul>
          </div>
        )}
      </Show>

      <div class="controls">
        <button
          id="btn-convert-stt"
          disabled={props.disabled || !canConvert()}
          onClick={props.onConvert}
        >
          Fill
        </button>
      </div>
    </section>
  );
};
